.wadi_flip_box_effect_flip .elementor-widget-container {
  transform-style: preserve-3d;
}

.wadi_flip_box {
  transition: 0.6s;
  transform-style: preserve-3d;
  position: relative;
  width: 100%;
  height: 280px;
}
.wadi_flip_box .wadi_flip_box_layer {
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
}
.wadi_flip_box .wadi_flip_box_layer_overlay {
  width: 100%;
  height: 100%;
}

.wadi_flip_box_effect_flip {
  perspective: 1000px;
}
.wadi_flip_box_effect_flip.wadi_flip_box_direction_ltr:hover .wadi_flip_box {
  transform: rotateX(0) rotateY(180deg);
}
.wadi_flip_box_effect_flip.wadi_flip_box_direction_ltr:hover .wadi_flip_box:hover .wadi_flip_box__front {
  z-index: 2;
  transform: rotateX(0) rotateY(-180deg);
  transform: none;
}
.wadi_flip_box_effect_flip.wadi_flip_box_direction_ltr .wadi_flip_box__back {
  transform: rotateX(0) rotateY(180deg);
}
.wadi_flip_box_effect_flip.wadi_flip_box_direction_rtl:hover .wadi_flip_box {
  transform: rotateX(0) rotateY(-180deg);
}
.wadi_flip_box_effect_flip.wadi_flip_box_direction_rtl:hover .wadi_flip_box:hover .wadi_flip_box__front {
  z-index: 2;
  transform: rotateX(0) rotateY(180deg);
  transform: none;
}
.wadi_flip_box_effect_flip.wadi_flip_box_direction_rtl .wadi_flip_box__back {
  transform: rotateX(0) rotateY(-180deg);
}
.wadi_flip_box_effect_flip.wadi_flip_box_direction_ttb:hover .wadi_flip_box {
  transform: rotateY(0) rotateX(-180deg);
}
.wadi_flip_box_effect_flip.wadi_flip_box_direction_ttb:hover .wadi_flip_box:hover .wadi_flip_box__front {
  z-index: 2;
  transform: rotateY(0) rotateX(-180deg);
  transform: none;
}
.wadi_flip_box_effect_flip.wadi_flip_box_direction_ttb .wadi_flip_box__back {
  transform: rotateY(0) rotateX(180deg);
}
.wadi_flip_box_effect_flip.wadi_flip_box_direction_btt:hover .wadi_flip_box {
  transform: rotateY(0) rotateX(180deg);
}
.wadi_flip_box_effect_flip.wadi_flip_box_direction_btt:hover .wadi_flip_box:hover .wadi_flip_box__front {
  z-index: 2;
  transform: rotateY(0) rotateX(180deg);
  transform: none;
}
.wadi_flip_box_effect_flip.wadi_flip_box_direction_btt .wadi_flip_box__back {
  transform: rotateY(0) rotateX(-180deg);
}
.wadi_flip_box_effect_flip.wadi_flip_box_3d .wadi_flip_box_layer_overlay {
  transform: translateZ(90px) scale(0.91);
}

.wadi_flip_box__back {
  background-color: #FFE7AE;
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.wadi_flip_box__back .wadi_flip_box_back_full_box {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99;
}

/* hide back of pane during swap */
.wadi_flip_box__front {
  background-color: #7585dc;
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  transform: none;
  width: 100%;
  height: 100%;
}

.wadi_flip_box_layer_overlay {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  padding: 35px;
  text-align: center;
  width: 100%;
  height: 100%;
}

.content_wrapper .wadi_flip_box_front_icon {
  display: inline-block;
}
.content_wrapper .wadi_flip_box_front_icon i {
  font-size: 50px;
}
.content_wrapper .wadi_flip_box_back_icon {
  display: inline-block;
}
.content_wrapper .wadi_flip_box_back_icon i {
  font-size: 50px;
}
.content_wrapper .wadi_flip_box_front_image img {
  width: 100px;
}
.content_wrapper .wadi_flip_box_back_image img {
  width: 100px;
}
.content_wrapper a.wadi_flip_box_back_url {
  display: inline-block;
  text-decoration: none;
}

.wadi_flip_box_effect_slide {
  width: 100%;
  perspective: 1000px;
}
.wadi_flip_box_effect_slide .wadi_flip_box {
  overflow: hidden;
}
.wadi_flip_box_effect_slide.wadi_flip_box_direction_ltr:hover .wadi_flip_box:hover .wadi_flip_box__back {
  z-index: 2;
  transform: none;
}
.wadi_flip_box_effect_slide.wadi_flip_box_direction_ltr .wadi_flip_box__back {
  transform: translateX(-100%) translateY(0);
}
.wadi_flip_box_effect_slide.wadi_flip_box_direction_rtl:hover .wadi_flip_box:hover .wadi_flip_box__back {
  z-index: 2;
  transform: none;
}
.wadi_flip_box_effect_slide.wadi_flip_box_direction_rtl .wadi_flip_box__back {
  transform: translateX(100%) translateY(0);
}
.wadi_flip_box_effect_slide.wadi_flip_box_direction_ttb:hover .wadi_flip_box:hover .wadi_flip_box__back {
  z-index: 2;
  transform: none;
}
.wadi_flip_box_effect_slide.wadi_flip_box_direction_ttb .wadi_flip_box__back {
  transform: translateY(-100%) translateX(0);
}
.wadi_flip_box_effect_slide.wadi_flip_box_direction_btt:hover .wadi_flip_box:hover .wadi_flip_box__back {
  z-index: 2;
  transform: none;
}
.wadi_flip_box_effect_slide.wadi_flip_box_direction_btt .wadi_flip_box__back {
  transform: translateY(100%) translateX(0);
}

.wadi_flip_box_effect_push {
  width: 100%;
  perspective: 1000px;
}
.wadi_flip_box_effect_push .wadi_flip_box {
  overflow: hidden;
}
.wadi_flip_box_effect_push.wadi_flip_box_direction_ltr .wadi_flip_box__front {
  transform: none;
}
.wadi_flip_box_effect_push.wadi_flip_box_direction_ltr:hover .wadi_flip_box:hover .wadi_flip_box__back {
  z-index: 2;
  transform: none;
}
.wadi_flip_box_effect_push.wadi_flip_box_direction_ltr:hover .wadi_flip_box:hover .wadi_flip_box__front {
  transform: translateX(100%) translateY(0);
}
.wadi_flip_box_effect_push.wadi_flip_box_direction_ltr .wadi_flip_box__back {
  transform: translateX(-100%) translateY(0);
}
.wadi_flip_box_effect_push.wadi_flip_box_direction_rtl .wadi_flip_box__front {
  transform: none;
}
.wadi_flip_box_effect_push.wadi_flip_box_direction_rtl:hover .wadi_flip_box:hover .wadi_flip_box__back {
  z-index: 2;
  transform: none;
}
.wadi_flip_box_effect_push.wadi_flip_box_direction_rtl:hover .wadi_flip_box:hover .wadi_flip_box__front {
  transform: translateX(-100%) translateY(0);
}
.wadi_flip_box_effect_push.wadi_flip_box_direction_rtl .wadi_flip_box__back {
  transform: translateX(100%) translateY(0);
}
.wadi_flip_box_effect_push.wadi_flip_box_direction_ttb .wadi_flip_box__front {
  transform: none;
}
.wadi_flip_box_effect_push.wadi_flip_box_direction_ttb:hover .wadi_flip_box:hover .wadi_flip_box__back {
  z-index: 2;
  transform: none;
}
.wadi_flip_box_effect_push.wadi_flip_box_direction_ttb:hover .wadi_flip_box:hover .wadi_flip_box__front {
  transform: translateX(0) translateY(100%);
}
.wadi_flip_box_effect_push.wadi_flip_box_direction_ttb .wadi_flip_box__back {
  transform: translateX(0) translateY(-100%);
}
.wadi_flip_box_effect_push.wadi_flip_box_direction_btt .wadi_flip_box__front {
  transform: none;
}
.wadi_flip_box_effect_push.wadi_flip_box_direction_btt:hover .wadi_flip_box:hover .wadi_flip_box__back {
  z-index: 2;
  transform: none;
}
.wadi_flip_box_effect_push.wadi_flip_box_direction_btt:hover .wadi_flip_box:hover .wadi_flip_box__front {
  transform: translateX(0) translateY(-100%);
}
.wadi_flip_box_effect_push.wadi_flip_box_direction_btt .wadi_flip_box__back {
  transform: translateX(0) translateY(100%);
}

.wadi_flip_box_effect_zoom_in {
  width: 100%;
  perspective: 1000px;
}
.wadi_flip_box_effect_zoom_in .wadi_flip_box .wadi_flip_box__back {
  -webkit-transition: opacity 0.5s 0.2s, -webkit-transform 0.7s;
  transition: opacity 0.5s 0.2s, -webkit-transform 0.7s;
  -o-transition: transform 0.7s, opacity 0.5s 0.2s;
  transition: transform 0.7s, opacity 0.5s 0.2s;
  transition: transform 0.7s, opacity 0.5s 0.2s, -webkit-transform 0.7s;
  opacity: 0;
  -webkit-transform: scale(0.7);
  -ms-transform: scale(0.7);
  transform: scale(0.7);
}
.wadi_flip_box_effect_zoom_in .wadi_flip_box:hover .wadi_flip_box__back {
  -webkit-transition: opacity 0.5s, -webkit-transform 0.7s;
  transition: opacity 0.5s, -webkit-transform 0.7s;
  -o-transition: transform 0.7s, opacity 0.5s;
  transition: transform 0.7s, opacity 0.5s;
  transition: transform 0.7s, opacity 0.5s, -webkit-transform 0.7s;
  opacity: 1;
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
}

.wadi_flip_box_effect_zoom_out {
  width: 100%;
  perspective: 1000px;
}
.wadi_flip_box_effect_zoom_out .wadi_flip_box .wadi_flip_box__front {
  -webkit-transition: opacity 0.35s, width 0.1ms, -webkit-transform 0.7s;
  transition: opacity 0.35s, width 0.1ms, -webkit-transform 0.7s;
  -o-transition: transform 0.7s, opacity 0.35s, width 0.1ms;
  transition: transform 0.7s, opacity 0.35s, width 0.1ms;
  transition: transform 0.7s, opacity 0.35s, width 0.1ms, -webkit-transform 0.7s;
  opacity: 1;
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
  z-index: 1;
  width: 100%;
}
.wadi_flip_box_effect_zoom_out:hover .wadi_flip_box__front {
  width: 0;
  opacity: 0;
  -webkit-transform: scale(0.7);
  -ms-transform: scale(0.7);
  transform: scale(0.7);
  -webkit-transition: opacity 0.7s 0.1s, width 0.1ms 0.7s, -webkit-transform 0.8s;
  transition: opacity 0.7s 0.1s, width 0.1ms 0.7s, -webkit-transform 0.8s;
  -o-transition: transform 0.8s, opacity 0.7s 0.1s, width 0.1ms 0.7s;
  transition: transform 0.8s, opacity 0.7s 0.1s, width 0.1ms 0.7s;
  transition: transform 0.8s, opacity 0.7s 0.1s, width 0.1ms 0.7s, -webkit-transform 0.8s;
}

.wadi_flip_box_effect_fade {
  width: 100%;
  perspective: 1000px;
}
.wadi_flip_box_effect_fade .wadi_flip_box .wadi_flip_box__back {
  opacity: 0;
  z-index: 2;
}
.wadi_flip_box_effect_fade .wadi_flip_box:hover .wadi_flip_box__back {
  z-index: 2;
  opacity: 1;
}